.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 21px 70px 93px;
  background-color: #073042;
}

.logo {
  display: block;
  width: 38px;
  height: 38px;
  background-image: url(../../images/header_logo.svg);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 20%;
  border: none;
  transition: opacity .5s ease-in-out;
}

.header__links {
  display: flex;
  align-items: center;
  gap: 16px;
}

.header__link {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-decoration: none;
}

.header__link_btn {
  display: flex;
  width: 76px;
  height: 32px;
  background-color: #2BE080;
  border-radius: 3px;
  border: none;
  transition: opacity .5s ease-in-out;
  justify-content: center;
  align-items: center;
}

.header__link_btn:hover {
  cursor: pointer;
  opacity: .7;
}

.header__link_text_black {
  color: black;
}

.header__link_text_white {
  transition: opacity .5s ease-in-out;
  color: #FFFFFF;
}

.header__link_text_white:hover {
  cursor: pointer;
  opacity: .7;
}

.header-container {
  display: flex;
}

@media (min-width: 513px) and (max-width: 911px) {
  .header {
    padding: 19px 30px 157px;
  }

  .header__links {
    gap: 30px;
  }

  .header__br {
    display: none;
  }
}

@media (min-width: 301px) and (max-width: 512px) {
  .header {
    padding: 18px 14px 88px;
  }

  .header__link_btn {
    width: 54px;
    height: 26px;
  }

  .header__link_text_white {
    transition: opacity .5s ease-in-out;
    color: #FFFFFF;
  }

  .header__link {
    font-size: 10px;
    line-height: 16px;
  }

  .header__br {
    display: none;
  }

}


@media (min-width: 301px) and (max-width: 512px) {
  .header__br {
    display: none;
  }
}
